
.brand__container{
  margin: 0 auto;
 background: rgb(246, 244, 244);
 padding-bottom: 20px;
 box-shadow: 0 10px 40px rgba(0,0,0,0.1);
 
 .content__section-header{
 font-family: "TTLakes",sans-serif;
 display: flex;
 background: linear-gradient(to right,rgb(246, 244, 244) 40%,white 60%);
 color: #868484;
 padding-top: 30px;
 padding-left: 10px;;
 min-height: 70px;
 border-top: #85fffb 1px solid;
 margin-bottom: 10px;
  }
}

.swiper__container{
 position: relative;
 width: 95%;
 height: 60%;
 margin-bottom: 10px;
 
 &.extended{
 display: grid; 
 grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); 
 gap: 20px;
 position: relative;
 width: 95%;
 height: 100%;
 margin-bottom: 10px;
 }

 .swiper-wrapper{
 display: flex;
 align-items: center;
 gap: 15px;
 }

 .swiper-slide{ width:240px;
 height: 74px;
 text-align: center;
 font-size: 15px;
 background: white;
 display: flex;
 justify-content: center;
 align-items: center; 
 border: 2px solid lightgrey;
 border-radius: 5px;
 }
 &.content__brand-card {
 .content__brand-logo{
  width:240px;
 height: 70px;
 text-align: center;
 font-size: 15px;
 background: white;
 display: flex;
 align-items: center;
 padding: 10px;   
 }

  .content__brand-arrow-btn{
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);

   img {
    width: 16px;
    margin-left: 8px;
    transition: transform 0.3s ease;   
    }
  }
  &:hover .content__brand-arrow-btn img {
    transform: translateX(5px);
    }
 }
 .swiper-pagination {
 position: sticky;
 text-align: center;
 transform: translateZ(0);
 transition: opacity .3s;
 z-index: 10;
 margin-top: 20px;
 }

 .swiper-button-next,
 .swiper-button-prev {
 display:none;
 }
}

.container-toggle{
 font-family: 'TTLakes', sans-serif;
 font-size: 1rem;
 font-weight: 400;
 display: block;
 cursor: pointer;
 color: #000000;
 border: none;
 border-radius: 0px;
 text-decoration: none;
 transition: all 0.3s  ease;
 background-color: rgb(248, 245, 245);  
 
 .content__arrow--img{
    width: 16px;
    transition: transform 0.3s ease;
 }
 &.rotated{
    transform:rotate(180deg)
 }
 &:hover {
   cursor:pointer;
   transform: scale(1.1); 
   color:#85fffb
 }
}

